import React, { ReactElement } from "react";
import { withRouter } from "react-router";
import { List, ListItem, ListItemText } from "@material-ui/core";

import { ReportingType } from "../api/reporting-types/model.interface";
import * as ReportingTypeAPI from "../api/reporting-types";
import withPageLayout from "../hocs/withPageLayout";
import { PageHeaderContext } from "../context/layout";
import AppBtn from "../components/form/AppBtn";

const ReportingTypeList = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);

  const [reportingTypeList, setReportingTypeList] = React.useState([]);

  const addMoreBtn: ReactElement = (
    <AppBtn title="Add" href="/reporting-types/add" />
  );

  React.useEffect(() => {
    props.setShowLoading(true);

    LayoutContext.setTitle("Reporting Types");
    LayoutContext.setExtra([addMoreBtn]);
    getData();
  }, []);

  const getData = async () => {
    const data: any = await ReportingTypeAPI.get();
    setReportingTypeList(data);
    props.setShowLoading(false);
  };
  return (
    <>
      <List>
        {reportingTypeList.map((reportingType: ReportingType) => {
          return (
            <ListItem
              button
              onClick={() =>
                props.history.push(`reporting-types/edit/${reportingType.id}`)
              }
            >
              <ListItemText
                primary={reportingType.name}
                secondary={reportingType.time}
              />
            </ListItem>
          );
        })}
      </List>
    </>
  );
};
export default withRouter(withPageLayout(ReportingTypeList));
